* {
    box-sizing: border-box;
    margin: 0;
}

html {
    scroll-behavior: smooth;
    overflow-y: hidden;
}

body {
    background-image: url(/img/background.svg);
    background-size: cover;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}
    
img {
    max-width: 100%;
}

.presentation {
    width: 80%;
    height: 60%;
    display: flex;
    justify-content: space-between;
}

.presentation_image {
    height: 90%;
    width: 30%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;

    h1 {
        font-family: Montserrat-SemiBold;
        font-style: normal;
        font-size: 38px;
        color: #FFFFFF;
    }

    p {
        font-family: Montserrat-Light;
        font-style: normal;
        font-size: 25px;
        color: #FFFFFF;
    }
}

.presentation_text {
    width: 65%;
    height: 100%;
    border: 1px solid #FFFFFF;
    box-sizing: border-box;

    p {
        font-family: Montserrat-Light;
        font-style: normal;
        font-size: 18px;
        color: #FFFFFF;
        margin: 10px;

        a {
            color: #FFFFFF;
            text-decoration: underline;
        }
    }
}

.contact {
    width: 50%;
    float: right;
    display: flex;
    justify-content: space-around;
    font-family: Montserrat-Regular;
    font-size: 16px;
    color:white;
    align-self: flex-end;

    a {
        color: white;
    }
}